<template>
    <nav class="user-navbar navbar navbar-default">
		<div class="container">
			<div class="navbar-header">
				<button type="button" class="navbar-toggle collapsed" data-toggle="collapse" data-target="#navbar">
					<span class="icon-bar"></span>
					<span class="icon-bar"></span>
					<span class="icon-bar"></span>
				</button>
				<router-link to="/" class="navbar-brand">作品集</router-link>
			</div>
			<div class="collapse navbar-collapse" id="navbar">
				<ul class="nav navbar-nav navbar-right">
					<li><router-link to="/">WebDesign</router-link></li>
					<li><router-link to="/Design">Design</router-link></li>
					<li><router-link to="/Contact">Contact</router-link></li>
				</ul>
			</div>
		</div>
	</nav>
</template>
<script>
    export default{
        name:'Navbar'
    }
</script>
<style lang="scss" scope>
$yellow_color:#FFF100;
.user-navbar{
	background-color: #333;
	border: 4px solid #fff;
	border-radius: 0;
	.navbar-header{
		.navbar-brand{
			color:#fff;
		}
	}
	.navbar-toggle{
		border: none;
		&:hover .icon-bar{
			background-color: $yellow_color;
		}
		&:focus{
			background-color: transparent;
		}
		.icon-bar{
			background-color: #fff;
		}
		&:hover{
			background-color: transparent;
		}
	}
	.navbar-nav>li>a{
		padding-top: 15px;
		padding-bottom: 15px;
		font-size: 16px;
	}
	#navbar{
		.nav{
			li{
				&:hover a{
					color:$yellow_color;
				}
				a{
					color:#fff;
				}
			}
		}
	}
}
@media (min-width:768px){
	.user-navbar{
		margin-bottom: 0;
		.navbar-brand{
			//height: 85px;
			font-size: 20px;
			//line-height: 85px - 30;
		}
		.navbar-nav>li>a{
			//padding-top: 32.5px;
			//padding-bottom: 32.5px;
			font-size: 20px;
		}
	}
}
</style>


